import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { PRIMARY, SECONDARY } from '../../utils/Constants';
import classNames from 'classnames';
import Spinner from '../Spinner/Spinner';
const Wrapper = styled.div`
    display : flex;
    align-items : center;
    justify-content: center;
    border-radius: 4px;
    background-color : ${props => props.backgroundColor};
    font-size : ${props => props.fontSize}px;
    font-weight : ${props => props.fontWeight};
    color : ${props => props.fontColor};
    width : ${props => props.width}px;
    border: ${props => props.borderSize}px solid ${props => props.defaultBorderColor};
    border : 1px solid ${props => props.backgroundColor};
    height : ${props => props.height}px;
    &:hover {
      border : 1px solid ${props => props.borderColor};
      cursor: pointer;
    }
    &.selected {
      border : 1px solid ${props => props.borderColor};
    }
  `  
export default function Button({label,fontColor = '#E0ECFF',type = PRIMARY,bgColor,defaultBorderColor = '#203B60',borderSize = 1, outline = false,isSelected = false,outlineColor = '#CD7A37',iconsrc ,onClick,width = 158,fontSize = 14,fontWeight = 'normal',height = 48,className,styles = {}}){
  const [pending, setPending] = useState(false)
  let backgroundColor ;
  if(bgColor) {
    backgroundColor = bgColor;
  } else if(type === PRIMARY) {
    backgroundColor = '#3756CD';
  } else if(type === SECONDARY){
    backgroundColor = '#203B60' 
  }
  const borderColor = outline ? outlineColor : 'none';
  
  const click = async () => {
    if(onClick && !pending){
      setPending(true);
      const result = await onClick();
      setPending(false)
    }
  }
  const clazz = classNames(className,{
    selected : isSelected
  })
  return (
    <Wrapper borderColor={borderColor} 
            backgroundColor={backgroundColor} 
            fontColor={fontColor} 
            fontSize={fontSize} 
            fontWeight={fontWeight} 
            width={width} 
            height={height} className={clazz} style={{...styles}} onClick={click}>
      {iconsrc && <img src={iconsrc} alt=''/>}{pending && <Spinner/>}{label}
    </Wrapper>
  )
}