import styled from "styled-components"
const images = require.context('../../assets/img', true)
const Wrapper = styled.img`
  width : ${props => props.width }px;
  height : ${props => props.height}px;
  display : flex;
  align-items : center;
`
export default function Icon({width,height,token}){
  const img = images(`./${token}.svg`)
  return <Wrapper src={img.default} width={width} height={height}/>
}