export const getUserBTokensInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  if (['v2_dpmm', 'v2_lite_dpmm', 'option'].includes(versionId)) {
    return await api(versionId).getUserBTokensInfo(
      chainId,
      poolAddress,
      accountAddress
    );
  } else {
    return { success: true, response: { data: [] } };
  }
};

